@keyframes flickerAnimation {
    0%   { opacity:1; }
    50%  { opacity:0; }
    100% { opacity:1; }
  }

@keyframes move1 {

    0%{
        bottom: 50%;
        right: 50%;
    }

    50%
    {
        bottom: 50%;
        right: 50%;
    }

    100%
    {
        bottom: 0px;
        right: 0px;
    }

}

@keyframes move2 {

    0%{
        top: 50%;
        left: 50%;
    }

    50%
    {
        top: 50%;
        left: 50%;
    }

    100%{
        top: 0px;
        left: 0px;
    }

}

@keyframes move3 {
